import React from 'react';
import styles from './styles.scss';

function Manage() {
    return ( <div className={styles.section} id="manage">
    		<div className={styles.main}>
        		<h1> RepOne helps you manage your team, your way.</h1>
        		<h2>The RepOne device fits the equipment you already have, and the flexible athlete management software works with your routine.</h2>
        	</div>
        </div>  
    );
}

export default Manage;



